  }
  .section{
    scroll-snap-align: start;
  }
} */




.section {
  display: flex;
  flex-direction: column;
  padding: 10% 7% 10% 10%;
  height: 80vh;
}

a {
  text-decoration: none;
  color: black;
}

.bold {
  font-weight: 400;
}

/*********** NAVBAR *************/
#navbar {
  height: 50vh;
  width: 5vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: fixed;
  bottom: 0%;
}
#navbar a{
  text-decoration: none;
}
#navbar img:hover{
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
#resume{
  height: 32px;
  width: 29px;
  border-radius: 8%;
}
#github{
  height: 32px;
  width: 32px;
  border-radius: 50%;
}
#email{
  height: 24px;
  width: 32px;
  border-radius: 8%;
}
#linkedin{
  height: 32px;
  width: 32px;
  border-radius: 8%;
}
#blog{
  height: 32px;
  width: 32px;
  border-radius: 8%;
}






/************ INTRO ***************/
.intro {
  padding-left: 17%;
}
.intro h3{
  font-weight: 300;
  font-size: 4vw;
}


/************ BACKGROUND ***********/
.background {
  flex-direction: row;
  justify-content: space-evenly;
}
#background-header{
  width: 25vw;
}
#background-content{
  width: 60vw;
  margin-left: 4%;
}
.background h3{
  text-align: center;
  margin-top: 0;
  font-size: 4vw;
  font-weight: 400;
  color: rgb(86, 138, 250);
}
.background p{
  margin-top: 1%;
  font-size: 2vw;
  font-weight: 200;
}

/***************** SKILLS ***************/
.skills {
  flex-direction: row;
  justify-content: space-evenly;
}
#skills-header{
  width: 25vw;
  margin: 6px;
}
#skills-content{
  display: flex;
  justify-content: center;
  width: 60vw;
  margin: 6px;
}
#skills-content h5{
  margin-top: 0;
  font-size: 2.5vw;
  font-weight: 300;
}
#skills-content p{
  font-size: 2vw;
  font-weight: 200;
  margin: 0;
}
#proficient{
  margin: 0 3vw 0 0;
}
#exposure{
  margin: 0 0 0 3vw;
}
.skills h3{
  padding-right: 5%;
  text-align: center;
  margin-top: 0;
  font-size: 4vw;
  font-weight: 400;
  color: rgb(86, 138, 250);
}


/************** PROJECTS *************/
.projects {
  flex-direction: row;
  justify-content: space-evenly;
}
.projects h3{
  padding-right: 5%;
  text-align: center;
  margin-top: 0;
  font-size: 4vw;
  font-weight: 400;
  color: rgb(86, 138, 250);
}
#projects-content h5:hover{
  cursor: pointer;
  font-weight: 500;
  color: rgb(101, 146, 244);
  /* text-shadow: 2px 2px 3px black; */
}
#projects-header{
  width: 25vw;
  margin: 6px;
}
#projects-content{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60vw;
  margin: 6px;
}
#projects-content h5{
  margin: 0;
  font-size: 2.5vw;
  font-weight: 300;
  color: rgb(86, 138, 250);
}
#projects-content p{
  font-weight: 200;
  font-size: 2vw;
}
.project-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5%;
}
.project-img-wrapper{
  width: 55vw;
  min-height: 25vh;
  align-items: center;
  display: flex;
  overflow: auto;
  border-radius: 10px;
  /* border: 2px solid lightgray; */
}
.project-img{
  max-height: 8em;
  margin: 0 3px 0 3px;
}
.project-img:hover{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
}
.chosen{
  width: 40vw;
  position: absolute;
  z-index: 1;
  padding-right: 6vw;
}
.blur{
  opacity: 0.3;
}



.quizcard {
  align-items: flex-end;
}
.quizcard p{
  text-align: right;
}


.fcebook {
  align-items: flex-start;
}
.fcebook p{
  text-align: left;
}


.cocktail {
  align-items: flex-end;
}
.cocktail p{
  text-align: right;
}